.top {
    background-image: url(../../image/backgrund.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }
  .container__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 100px;
    padding-bottom: 130px;
  }
  .top__left-block {
    width: 520px;
  }
  .top__logo {
    height: 58px;
    margin-bottom: 30px;
  }
  .top__title {
    font-size: 36px;
    font-family: "Open Sans";
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 40px;
  }
  .top__title:after {
    content: "";
    display: block;
    width: 64px;
    border: 1px solid #fff;
    margin-top: 40px;
  }
  .top__subtitle {
    font-size: 16px;
    font-family: "Open Sans";
    color: #fff;
    line-height: 1.438;
    margin-bottom: 45px;
  }
  .top__button {
    display: inline-block;
    border: 2px solid #fff;
    color: #fff;
    font-size: 15px;
    font-family: "Open Sans";
    line-height: 1.2;
    background-color: transparent;
    padding: 13px 30px;
    text-decoration: none;
  }
  .top__button:hover {
    color: #000;
    background-color: #fff;
  }
  .top__form-wrap {
    max-width: 360px;
    border-radius: 6px;
    background-color: #fff;
    overflow: hidden;
  }
  .top__form-title {
    font-size: 20px;
    font-family: "Open Sans";
    color: rgb(51, 51, 51);
    font-weight: bold;
    line-height: 1.2;
    text-align: center;
    padding: 18px 0;
    background-color: #f5f5f5;
  }
  .top__form-title_upper {
    text-transform: uppercase;
  }
  .top__form-input_wrap {
    padding: 30px 38px 0;
    margin-bottom: 13px;
  }
  .top__form-input {
    width: 100%;
    padding: 10px;
    border: none;
    border-bottom: 1px solid #d6d6d6;
    box-sizing: border-box;
    margin-bottom: 35px;
  }
  .top__form-button {
    width: 100%;
    text-align: center;
    padding: 18px 0;
    border: none;
    color: #fff;
    font-size: 15px;
    font-family: "Open Sans";
    line-height: 1.2;
    cursor: pointer;
  }
  .top__form-button:hover {
    opacity: 0.8;
  }