.social__hat {
    padding-top: 25px;
    padding-bottom: 25px;
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.56);
    position: relative;
  }
  .social__media {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .social__media-text {
    font-family: "Open Sans";
    text-align: left;
    width: 365px;
  }
  .social__media-text-titel {
    font-size: 20px;
    color: rgb(51, 51, 51);
    font-weight: bold;
    margin-bottom: 10px;
  }
  .social__media-text-subtitel {
    font-size: 14px;
    color: rgb(134, 135, 139);
    line-height: 1.429;
  }
  .icons {
    width: 650px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .icon__link {
    text-decoration: none;
    color: #cccccc;
    font-size: 45px;
  }
  .icon__link:hover {
    color: #000000;
  }